import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    userName: '',
    user: null,
    repos: null   
  },
  getters: {
  },
  mutations: {
    setUserName(state, payload) {
      state.userName = payload
    },
    getUser(state, info) {
      state.user = info
    },
    getRepos(state, info) {
      state.repos = info
    },
    delUser(state) {
      state.repos = null
      state.user = null
      state.userName = ''
    }
  },
  actions: {
    async getUser(context, userName) {
      try {
        let reque1 = await axios.get(`https://api.github.com/users/${userName}`)
        let reque2 = await axios.get(`https://api.github.com/users/${userName}/repos`)      
        context.commit('getUser', reque1)
        context.commit('getRepos', reque2)        
      } catch (error) {
        console.error('Произошла ошибка при получении данных');
      }
    },
  },
})
