<template>
    <div class="main__search">
        <input @keydown.enter="getUser(getUserName), getRepos()" v-model="getUserName" type="text" placeholder="Введите имя пользователя">
        <button @click="delUser()" v-if="getUserName.length > 0" class="main__search-del">X</button>
        <button @click="getUser(getUserName), getRepos()" class="main__search-btn">НАЙТИ</button>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';

    export default {
        computed: {
            ...mapState(['userName']),
            getUserName: {
                get() {
                    return this.userName
                },
                set(val) {
                    this.setUserName(val)
                }
            }
        },
        methods: {
            ...mapMutations(['setUserName']),
            ...mapMutations(['delUser']),
            ...mapActions(['getUser']),
            ...mapMutations(['getRepos'])
        },
    }
</script>