<template>
    <main class="main">
        <Find/>
        <div v-if="user" class="main__user">
            <div class="main__user-avatar">
                <img :src="user.data.avatar_url" alt="">
                <a :href="user.data.html_url" target="_blank" class="main__user-btn">ПОСЕТИТЬ</a>
            </div>
            <div class="main__user-info">
                <span>{{ user.data.login }}</span>
                <span>Репозиториев:<p>{{ user.data.public_repos }}</p></span>
                
                <span>Создан:<p>{{ getDate(user.data.created_at) }}</p></span>
                <span>Подписчиков:<p>{{ user.data.followers }}</p></span>
                <span>Подписок:<p>{{ user.data.following  }}</p></span>     
            </div>
        </div>
        <div v-if="user" class="main__sorting">
            <h4 class="main__sorting-title">Сортировка</h4>
            <div class="main__sorting-btns">
                <button
                    @click="active = 1"
                    :class="{ active: active == 1 }"
                    class="main__sorting-btn">
                    ИМЯ
                </button>
                <button
                    @click="active = 2"
                    :class="{ active: active == 2 }"
                    class="main__sorting-btn">
                    ЗВЕЗДЫ
                </button>
                <button
                    @click="active = 3"
                    :class="{ active: active == 3 }"
                    class="main__sorting-btn">
                    ДАТА
                </button>
            </div>
        </div>
        <div v-if="user" class="main__content">
            <NameContent
                v-if="active == 1"
            />
            <StarsContent
                v-if="active == 2"
            />
            <DateContent
                v-if="active == 3"
            />
        </div>
    </main>
</template>

<script>
import Find from './Find.vue';
import NameContent from './NameContent.vue';
import StarsContent from './StarsContent.vue';
import DateContent from './DateContent.vue';
import { mapState } from 'vuex'

    export default {
        data() {
            return {
                active: 1
            }
        },
        components: { 
            Find,
            NameContent,
            StarsContent,
            DateContent 
        },
        computed: {
            ...mapState(['sortingBtn']),
            ...mapState(['user']),
            ...mapState(['repos'])
        },
        methods: {
            getDate(date) {
                return new Date(date).toLocaleDateString('ru-RU', {
                    day: 'numeric',
                    month: 'numeric',
                    year: 'numeric'
                })
            },
        },
    }
</script>