<template>
    <div v-for="item in repos.data" :key="item.id" class="main__content-item">
        <div class="main__content-item-info">
            <h3>{{ item.name }}</h3>
            <p>Кол-во звёзд: {{ item.stargazers_count }}</p>
            <p>Дата добавления: {{ getDate(item.created_at) }}</p>
        </div>
        <a :href="item.html_url" target="_blank" class="main__content-item-btn">ПОСЕТИТЬ</a>
    </div>
</template>

<script>
import { mapState } from 'vuex'

    export default {
        computed: {
            ...mapState(['repos']),
        },
        methods: {
            getDate(date) {
                return new Date(date).toLocaleDateString('ru-RU', {
                    day: 'numeric',
                    month: 'numeric',
                    year: 'numeric'
                })
            },
            sortByDate() {
                this.repos.data.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
            },
        },
        created() {
            this.sortByDate()
        }
    }
</script>